body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: TerrariaFont;
    src: url(/terrabuild/static/media/ANDYB.775c3814.TTF);
}

#buildSummary {
    background-color: var(--surface);
    height: 35vh;
    text-align: center;
    font-family: TerrariaFont, serif;
    box-shadow: 0 0 0 2px var(--primary); /* emulate the border */
}
#buildSummary h3 {
    padding-top: 1vh;
    color: var(--primary);
    margin: 0;
    font-size: 5vh;
}
#buildSummary p {
    margin: 0;
    font-size: 3.5vh;
    color: var(--on-surface);
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: var(--on-surface);
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 1;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}
@font-face {
    font-family: TerrariaFont;
    src: url(/terrabuild/static/media/ANDYB.775c3814.TTF);
}

#header {
    background-color: var(--surface);
    height: 7vh;
    text-align: center;
    font-family: TerrariaFont, serif;
    box-shadow: 0 0 0 2px var(--primary); /* emulate the border */
}
#header h1 {
    padding-top: 1vh;
    color: var(--primary);
    margin: 0;
    font-size: 5vh;
}
.itemDisplay {
    background-color: var(--surface);
    font-family: TerrariaFont, serif;
    color: var(--on-surface);
    aspect-ratio: 1 / 1;
    border: var(--on-background) solid 3px;
    border-radius: 10px;
    cursor: pointer;
    vertical-align: top;
    display: inline-block;
    text-align: center;
    margin: 10px;
}

@media screen and (orientation: portrait) {
    .itemDisplay {
        width: 20vw;
        aspect-ratio: 1 / 1.75;
    }

    .itemCaption {
        font-size: 2vh;
        max-font-size: 2vh;
    }
}
@media screen and (orientation: landscape) {
    .itemDisplay {
        width: 10vw;
        aspect-ratio: 1 / 1;
    }

    .itemCaption {
        font-size: 3vh;
        max-font-size: 3vh;
    }
}

.itemImageContainer {
    padding-top: 2vh;
    width: 60%;
    aspect-ratio: 1 / 1;
    margin: 0 auto;
    display: block;
}

.itemImage {
    width: 100%;
    image-rendering: pixelated;
}

.itemCaption {
    font-family: TerrariaFont, serif;
    color: var(--on-surface);
    display: block;
    text-overflow: ellipsis;
    word-wrap: break-word;
    -webkit-text-size-adjust: auto;
}
@font-face {
    font-family: TerrariaFont;
    src: url(/terrabuild/static/media/ANDYB.775c3814.TTF);
}

#armorModal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
#armorModalContent {
    background-color: var(--background);
    color: var(--on-background);
    margin: 5vh 5vw auto; /* 15% from the top and centered */
    padding: 5vh 5vw;
    width: 80vw; /* Could be more or less, depending on screen size */
    height: 80vh;
    align-content: center;
    text-align:center;
    box-shadow: 0 0 0 2px var(--primary); /* emulate the border */
}

.selectionContainer {
    border-radius: 5px;
    width: 80vw;
    height: 60vh;
    overflow-y: auto;
}

/* Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: var(--primary) var(--on-primary);
}

/* Chrome, Edge and Safari */
*::-webkit-scrollbar {
    width: 10px;
    width: 10px;
}
*::-webkit-scrollbar-track {
    border-radius: 5px;
    background-color: var(--on-primary);
}

*::-webkit-scrollbar-track:hover {
    background-color: var(--on-primary);
}

*::-webkit-scrollbar-track:active {
    background-color: var(--on-primary);
}

*::-webkit-scrollbar-thumb {
    border-radius: 5px;
    background-color: var(--primary);
}

*::-webkit-scrollbar-thumb:hover {
    background-color: var(--primary);
}

*::-webkit-scrollbar-thumb:active {
    background-color: var(--primary);
}

:root {
  --background: #121212;
  --surface: #1e1e1e;
  --primary: #73ff56;
  --secondary: #03dac6;
  --on-background: #dddddd;
  --on-surface: #e2e2e2;
  --on-primary: #050307;
  --on-secondary: #000303;
}

@font-face {
  font-family: TerrariaFont;
  src: url(/terrabuild/static/media/ANDYB.775c3814.TTF);
}

#buildSelection {
  font-family: TerrariaFont, serif;
  background-color: #121212;
  background-color: var(--background);
  height: 58vh;
  width: 100%;
  font-size: 3vh;
  margin: 0 auto;
  display: block;
  text-align:center;
  box-shadow: 0 0 0 2px #73ff56;
  box-shadow: 0 0 0 2px var(--primary); /* emulate the border */
}

select {
  font-family: TerrariaFont, serif;
  font-size: 3vh;
}
